<template>
  <div class="home">
    <v-container>

      <v-row>

        <v-col cols="12" sm="6">
          <v-card class="mb-4 pa-3" color="grey lighten-3">

            <v-form ref="form" @submit.prevent="agregarTarea">
              <v-text-field color="teal" class="mb-3" label="Titulo" v-model="titulo" :rules="[v => !!v || 'Debe colocar titulo']"></v-text-field>
              <v-textarea color="teal" outlined label="Nota" v-model="descripcion" :rules="[v => !!v || 'Debe colocar una descripcion']"></v-textarea>
              <v-btn block rounded dark color="teal" type="submit">Agregar</v-btn>
            </v-form>

          </v-card>
        </v-col>

        <v-col cols="12" sm="6">

          <v-card color="grey lighten-3" class="mb-3" elevation="10" v-for="(item, index) in listaTareas" :key="index">
            <v-card-title>
              <v-chip
                class="ma-2"
                color="purple darken-4"
                text-color="white"
              >
                <v-avatar left>
                  <v-icon>label</v-icon>
                </v-avatar>
                {{item.titulo}}
              </v-chip>
            </v-card-title>
            <v-card-text>
              <p>{{item.descripcion}}</p>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="teal" class="ml-2" outlined rounded small @click="editarTarea(index)">Editar</v-btn>
              <v-btn color="amber" outlined rounded small justify="right" @click="eliminarTarea(item.id)">Eliminar</v-btn>
            </v-card-actions>
          </v-card>

        </v-col>

      </v-row>

      <v-snackbar
        v-model="snackbar"
        multi-line
        top
      >
        {{ mensaje }}
        <v-btn
          color="red"
          text
          @click="snackbar = false"
        >
          Cerrar
        </v-btn>
      </v-snackbar>

      <v-dialog v-model="dialog" persistent width="500">
        <v-card>

          <v-form ref="form" @submit.prevent="guardarCambios" class="pa-4">
            <v-text-field color="teal" class="mb-3" label="Titulo" v-model="titulo1" :rules="[v => !!v || 'Debe colocar titulo']"></v-text-field>
            <v-textarea color="teal" outlined label="Nota" v-model="descripcion1" :rules="[v => !!v || 'Debe colocar una descripcion']"></v-textarea>
            <v-btn block rounded dark color="teal" type="submit" @click="dialog = false">Guardar</v-btn>
            <v-btn block rounded dark color="error"  class="mt-1" @click="dialog = false;
            ">Salir</v-btn>
          </v-form>

        </v-card>
      </v-dialog>

    </v-container>
  </div>
</template>

<script>

export default {
  name: 'home',
  data() {
    return {
      listaTareas: [],
      titulo: '',
      titulo1: '',
      descripcion: '',
      descripcion1: '',
      snackbar: false,
      mensaje: '',
      dialog: false,
      indexTarea: ''
    }
  },
  methods: {
    agregarTarea(){
      console.log(this.titulo, this.descripcion);
      if(this.titulo === '' || this.descripcion === ''){
        console.log('Vacios');
        this.snackbar = true;
        this.mensaje = 'Debe llenar todos los campos';
      }else{
        this.listaTareas.push({id: Date.now(), titulo: this.titulo, descripcion: this.descripcion});
        localStorage.setItem('notas', JSON.stringify(this.listaTareas));
        this.snackbar = true;
        this.mensaje = 'Nota agregada';
        this.titulo = '';
        this.descripcion = '';
        this.$refs.form.reset();
      }
    },
    eliminarTarea(id){
      console.log(id);
      this.listaTareas = this.listaTareas.filter((e) => {
        return e.id != id;
      });
      localStorage.setItem('notas', JSON.stringify(this.listaTareas));
    },
    editarTarea(index){
      this.dialog = true;
      this.titulo1 = this.listaTareas[index].titulo;
      this.descripcion1 = this.listaTareas[index].descripcion;
      this.indexTarea = index;
    },
    guardarCambios(){
      this.dialog = false;
      this.listaTareas[this.indexTarea].titulo = this.titulo1;
      this.listaTareas[this.indexTarea].descripcion = this.descripcion1;
      localStorage.setItem('notas', JSON.stringify(this.listaTareas));
      this.$refs.form.reset();
      this.$refs.form.resetValidation();
      this.snackbar = true;
      this.mensaje = 'Se guardaron los cambios';
    }
  },
  created() {
    let notasDB = JSON.parse(localStorage.getItem('notas'));
    if(notasDB === null){
      this.listaTareas = [];
    }else{
      this.listaTareas = notasDB;
    }
  },
}
</script>
